<template>
  <div id="app">
    <div class="top">
      <button>&lt;</button>
      意见反馈
    </div>
    <div class="info">反馈信息</div>
    <div class="phone">
      手机号
      <input type="text" placeholder="请输入手机号码" />
    </div>
    <div class="feed">
      <label for="font"> 反馈类型</label>
      <select name="fun" id="">
        <option value="1">功能意见</option>
        <option value="2">2</option>
        <option value="3">3</option>
      </select>
    </div>
    <div class="option">您的意见</div>

    <textarea class="inp" type="text" v-model="items.text" ref="count" placeholder="请输入您的意见我们将不断优化体验"/>
    <div v-html="number"></div>

    <button class="send">
      提交
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      firstname: "woc",
      number: "100",
      items: {
        text: "",
      },
    };
  },
  watch: {
    //watch()监听某个值（双向绑定）的变化，从而达到change事件监听的效果
    items: {
      handler: function () {
        var _this = this;
        var _sum = 100; //字体限制为100个
        _this.$refs.count.setAttribute("maxlength", _sum);
        _this.number = _sum - _this.$refs.count.value.length;
      },
      deep: true,
    },
  },
};
</script>

